import React, {Component} from 'react';
import objectAssign from 'object-assign';

import './App.css';
import {Roll} from "./model/Roll";
import {Dice} from "./model/Dice";
import {MuiThemeProvider, RaisedButton} from "material-ui";
import TextField from 'material-ui/TextField';

class App extends Component {
  constructor(props) {
    super(props);
    this.updateInput = this.updateInput.bind(this);
    this.updateRoll = this.updateRoll.bind(this);
    this.state = {
      input: '',
      roll: null,
      dice: null
    };
  }

  updateInput(e) {
    const value = e.target.value;
    const dice = Dice.isDiceNotation(value) && Dice.fromNotation(value);
    const roll = Roll.fromNotation(value);
    this.setState(objectAssign({}, this.state,
      {
        input: value,
        roll: roll
      }
    ));
  }

  updateRoll() {
    if (this.state.roll) {
      this.setState(objectAssign({}, this.state, {result: this.state.roll.getResult()}));
    }
  }

  render() {
    return (
      <MuiThemeProvider>
        <main className="main">
          <TextField hintText="Dice expression" value={this.state.input} onChange={this.updateInput}/>
          <RaisedButton label="Roll" primary={true} onClick={this.updateRoll} style={{margin:'1em'}}/>
          <div>Results: {this.state.result && this.state.result.diceResults.map(e => e.join(',')).join('||')}</div>
          {this.state.result &&
          this.state.result.sumEach.length > 1 &&
          <div>Each: {this.state.result && this.state.result.sumEach.join('||')}</div>}
          <div>Total: {this.state.result && this.state.result.sumAll}</div>
        </main>
      </MuiThemeProvider>
    );
  }
}

export default App;



